import React, { useState, useEffect } from 'react';
import axios from 'axios';

function Software() {
	const [works, setWorks] = useState([]);
	const fetchData = () => {
		return axios
			.get(
				'https://portfolio-updated-69-default-rtdb.asia-southeast1.firebasedatabase.app/projects.json'
			)
			.then((response) => {
				let data = response.data.slice(1);
				data = data.sort(function (a, b) {
					return -(a.year - b.year);
				});
				setWorks(data);
			});
	};

	useEffect(() => {
		fetchData();
	}, []);

	return (
		<div>
			{works.map((e, i) => (
				<div key={i} className="work-item scale-in-hor-right-no-delay">
					<div className="work-item-names">
						<h2>{e.title}</h2>
						<p>- {e.type}</p>
					</div>
					<div className="work-item-year">{e.year}</div>
				</div>
			))}
		</div>
	);
}

export default Software;
